
import {map} from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';


import { AppSettingsService } from '../../../app/services/settings/app-settings.service';

@Injectable()
export class ConfigurationService {

	constructor(private http: Http) {
   		
   	}

   	public saveCoverPicPos(postObj) {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		return this.http.post(AppSettingsService.API_ENDPOINT("local")+"/api/saveCoverPicPos", postObj, options).pipe(
			map((res:Response) => res.json()));
	}


}
