
import {map} from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';


import { AppSettingsService } from '../../../app/services/settings/app-settings.service';

@Injectable()
export class NotificationService {

  	constructor(private http: Http) { }

	public updateNotificationDisplay(postObj) {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		return this.http.post(AppSettingsService.API_ENDPOINT("local")+"/api/updateNotificationDisplay", postObj, options).pipe(
		map((res:Response) => res.json()));
	}

	public checkNotification(postObj) {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		return this.http.post(AppSettingsService.API_ENDPOINT("local")+"/api/checkNotification", postObj, options).pipe(
		map((res:Response) => res.json()));
	}
	
	public fetchAllNotifications(postObj) {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		return this.http.post(AppSettingsService.API_ENDPOINT("local")+"/api/fetchAllNotifications", postObj, options).pipe(
		map((res:Response) => res.json()));
	}

}
